








import { Component, Vue, Prop } from 'vue-property-decorator'
import { PresenceStream } from 'remote-presence-connector'
import { PresenceStruct } from 'remote-presence-utils'
import PresentiPresence from './PresentiPresence.vue'

export interface Presence {
  name: string;
  type: string;
  url: string | null;
  details: string | null;
  state: string | null;
  applicationID: string | null;
  timestamps?: {
    start: string | null;
    end: string | null;
  };
  party: string | null;
  assets?: {
    largeText?: string;
    smallText?: string;
    largeImage?: string;
    smallImage?: string;
  };
  flags: number;
  emoji: string | null;
  createdTimestamp: number;
  data?: any;
}

export interface PresenceResponse {
  userID: string;
  guild: string;
  status: string;
  activities: Presence[];
  spotifyAssets: {
    [tag: string]: {
      url: string;
      palette: string[];
    };
  };
}

export type SpotifyAssets = PresenceResponse['spotifyAssets'];

interface PresenceState {
  gradient?: {
    color?: string;
    transition?: string;
  };
}

@Component({
  components: {
    PresentiPresence
  }
})
export default class DiscordStatus extends Vue {
  presences: PresenceStruct[] = [];
  presenceState: PresenceState = {};
  stream: PresenceStream = null!;
  effective: number = Date.now();
  state: any = null;
  
  @Prop()
  url: string;

  @Prop()
  scope: string;

  created () {
    this.respawnSocket()
  }

  respawnSocket () {
    this.stream = new PresenceStream(this.scope, { url: this.url })
    this.stream.on('presence', (activities) => {
      window.parent.postMessage(JSON.stringify({presence: activities}), location.origin);
      Vue.set(this, "presences", activities)
    })
    this.stream.on('state', (state) => { Vue.set(this, "state", state) })
    this.stream.connect()
  }
}
